html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 2rem;
  line-height: 1.5;
}

p {
  font-size: 2rem;
  color: #000;
}

a {
  text-decoration: none;
  background-color: transparent;
}

img {
  display: block;
  max-width: 100%;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

ul {
  padding: 0;
}

li {
  list-style: none;
}

.contenedor {
  max-width: 1200px;
  width: 95%;
  margin: 0 auto;
}

.text-center {
  text-align: center;
}

.card__price {
  color: #e99401;
  font-size: 6rem;
  font-weight: 900;
  line-height: 0;
}

.card__btn--blanco {
  font-size: 1.4rem;
  text-transform: uppercase;
  background-color: transparent;
  color: #fff;
  border: 2px solid #e99401;
  font-weight: 700;
  margin-top: 2rem;
  padding: 1rem 5rem;
  transition: 0.3s all ease;
}
.card__btn--blanco:hover {
  background-color: #e99401;
  color: #fff;
  cursor: pointer;
}

.card__btn--negro {
  font-size: 1.4rem;
  text-transform: uppercase;
  background-color: transparent;
  color: #000;
  border: 2px solid #e99401;
  font-weight: 700;
  margin-top: 2rem;
  padding: 1rem 5rem;
  transition: 0.3s all ease;
}
.card__btn--negro:hover {
  background-color: #e99401;
  color: #fff;
  cursor: pointer;
}

.notavif.notwebp .header {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../img/header.jpg");
}

.avif .header {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../img/header.avif");
}

.webp.nofavif .header {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../img/header.webp");
}

.visually-hidden {
  position: absolute !important;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip-path: inset(50%);
  white-space: nowrap;
  border: 0;
}

.header {
  padding: 5rem 0;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  z-index: -1;
}
.header__img {
  display: none;
  max-width: 50%;
}
@media (min-width: 992px) {
  .header__img {
    display: block;
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: -1;
    opacity: 0;
    animation-name: header__guitarra;
    animation-delay: 0.5s;
    animation-duration: 1s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
  }
}
.header__nav {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__nav {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header__nav img {
  width: 30rem;
}

.nav {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .nav {
    margin-top: 0;
  }
}
.nav ul {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  margin: 0;
}
.nav ul a {
  padding: 0.5rem;
  display: block;
}
.nav__element {
  color: #fff;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 700;
}

@keyframes header__guitarra {
  from {
    opacity: 0;
    right: 5rem;
  }
  to {
    opacity: 1;
    right: 0;
  }
}
@media (min-width: 768px) {
  .hero {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0;
  }
}
.hero__card {
  color: #fff;
  text-align: center;
}
@media (min-width: 768px) {
  .hero__card {
    text-align: left;
  }
}
.hero__card .card__title {
  font-size: 8rem;
  margin: 5rem 0;
}
.hero__card .card__text {
  text-align: center;
  color: #fff;
}
@media (min-width: 768px) {
  .hero__card .card__text {
    text-align: left;
  }
}

.coleccion__title {
  color: #e99401;
  font-weight: 900;
  text-transform: capitalize;
  font-size: 4rem;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .coleccion__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}
@media (min-width: 992px) {
  .coleccion__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
  }
}
.coleccion .card {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  grid-template-columns: 1fr 1.5fr;
  margin-bottom: 5rem;
  align-items: center;
}
@media (min-width: 768px) {
  .coleccion .card {
    margin-bottom: 0;
  }
}
.coleccion .card__title {
  text-transform: uppercase;
  margin: 3rem 0;
  font-size: 3rem;
}
.coleccion .card__p {
  font-size: 1.6rem;
}
.coleccion .card__price {
  line-height: 1.5;
  margin: 0;
}
.coleccion .card__btn--negro {
  display: block;
  text-align: center;
  font-size: 1.2rem;
  font-weight: 900;
}

.avif .cursos {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.6)), url("../img/cursos_bg.avif");
}

.notavif.webp .cursos {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.6)), url("../img/cursos_bg.webp");
}

.notavif.notwebp .cursos {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.6)), url("../img/cursos_bg.jpg");
}

.cursos {
  margin: 10rem 0 0;
  padding: 10rem 0;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (min-width: 768px) {
  .cursos__contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.cursos__contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .cursos__contenido {
    grid-column: 2/3;
  }
}
.cursos__title {
  color: #e99401;
  text-align: center;
  font-size: 4rem;
  font-weight: 900;
  margin: 5rem 0;
}
.cursos__p {
  color: #fff;
  font-size: 2.4rem;
  text-align: center;
}
.cursos .card__btn--blanco {
  width: fit-content;
  margin: 2rem auto 0;
}

.blog__title {
  color: #e99401;
  font-size: 4rem;
  font-weight: 900;
  text-transform: capitalize;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .blog__contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}
@media (min-width: 992px) {
  .blog__contenedor {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 4rem;
  }
}

.card-blog__info {
  padding: 2rem;
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .card-blog__info {
    margin-bottom: 0;
  }
}
.card-blog__title {
  font-size: 2.8rem;
  font-weight: 400;
  margin: 0 0 1rem;
  line-height: 1.2;
}
.card-blog__p {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
  font-size: 1.6rem;
  overflow: hidden;
}
.card-blog__p--amarillo {
  color: #e99401;
  margin: 0;
  font-size: 1.6rem;
}
.footer {
  background-color: #000;
  padding: 5rem 0;
  margin-top: 5rem;
}
.footer ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
}
@media (min-width: 768px) {
  .footer ul {
    flex-direction: row;
    flex: 2;
    gap: 2rem;
  }
}
.footer ul a {
  font-size: 2.8rem;
  text-transform: capitalize;
  font-weight: 700;
}
.footer__p {
  color: #fff;
  font-size: 2.8rem;
  font-weight: 700;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__p {
    flex: 1;
  }
}
.footer__contenedor {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .footer__contenedor {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}